import { Inject, Injectable } from '@angular/core';
import { Observable, of } from 'rxjs';
import { LOCAL_STORAGE, StorageService } from 'ngx-webstorage-service'

import { HttpClient } from '@angular/common/http';

import { QueueStatus } from 'src/models/status';
import { ChannelDiscovery, ListDiscovery } from 'src/models/discovery';

const AUTH_TOKEN_STORAGE_KEY = "ytpm-auth";

@Injectable({
  providedIn: 'root'
})
export class YtpmService {
  private baseUrl = '/api/client';
  private token = '';

  constructor(private http: HttpClient, @Inject(LOCAL_STORAGE) private storage: StorageService) { 
    this.token = storage.get(AUTH_TOKEN_STORAGE_KEY);
  }

  getStatus(lastUpdate: number = 0): Observable<QueueStatus> {
    return this.http.get<QueueStatus>(
      `${this.baseUrl}/a/poll/v2?token=${this.token}&since=${lastUpdate}`
    );
  }

  getChannels(): Observable<ChannelDiscovery[]> {
    return this.http.get<ChannelDiscovery[]>(
      `${this.baseUrl}/discovery/channels`
    );
  }

  getLists(): Observable<ListDiscovery[]> {
    return this.http.get<ListDiscovery[]>(
      `${this.baseUrl}/discovery/lists`
    ); 
  }

  getQueue(): Observable<any> {
    return this.http.get(`${this.baseUrl}/a/queue_state?token=${this.token}`);
  }

  getHistory(): Observable<any[]> {
    return this.http.get<any[]>(`${this.baseUrl}/a/play_history?token=${this.token}`);
  }

  getAutoComplete(term: string): Observable<string[]> {
    return this.http.get<string[]>(`${this.baseUrl}/autocomplete?q=${encodeURIComponent(term)}`);
  }

  getSearchResults(term: string, page?: string): Observable<any> {
    const pageQs = page ? `&page=${page}` : '';
    return this.http.get<any>(`${this.baseUrl}/search?q=${encodeURIComponent(term)}${pageQs}`);
  }

  sendCommand(command: string) {
    this.http.post(`${this.baseUrl}/a/send_command?token=${this.token}`, {command: command}).subscribe(() => undefined);
  }

  checkToken(): Observable<{valid: boolean}> {
    if (!this.token) {
      return of({valid: false});
    }
    return this.http.get<{valid: boolean}>(`${this.baseUrl}/auth/validate?token=${this.token}`);
  }

  auth(name: string, key: string) {
    this.http.get<{token: string}>(`${this.baseUrl}/auth?auth=${key}&name=${name}`).subscribe((response) => {
      this.setToken(response.token);
    });
  }

  deauth() {
    if (!this.token) {
      return;
    }
    this.http.get(`${this.baseUrl}/a/deauth?token=${this.token}`).subscribe(() => undefined);
    this.storage.remove(AUTH_TOKEN_STORAGE_KEY);
  }

  addToQueue(args: {videoId: string, front?: boolean, noinfluence?: boolean}) {
    let addToFront = args.front ? `&next=true` : '';
    let noInfluence = args.noinfluence ? `&noinfluence=true` : '';
    return this.http.get(`${this.baseUrl}/a/enqueue?token=${this.token}&videoId=${args.videoId}${addToFront}${noInfluence}`);
  }

  setToken(token: string){
    this.storage.set(AUTH_TOKEN_STORAGE_KEY, token);
    this.token = token;
  }
}
